html {
  height: 100%;
}
body {
  min-height: 100%;
  margin: 0;
  /*background-color: rgb(196, 226, 255);*/
  background-image: url(data:image/png;base64,);
}

img {
  width: 100%;
}

#root {
  position: absolute;
  min-height: 100%;
  height: 100%;
}
.App {
  min-height: 100%;
  height: 100%;
  text-align: center;
}

.Container {
  width: 90%;
  min-height: 100%;
  margin: auto;
  background-color: white;
}

.Header h1 {
  font-family: "Bodoni MT";
  padding: 1%;
}

.Header {
  width: 90%;
  margin: auto;
}

.AutoCompleteSearch {
  width: 90%;
  margin: auto;

  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.73);
  position: relative;
}

.AutoCompleteSearch input {
  width: 100%;
  border: 1px solid gray;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 4px 1px rgba(0, 0, 0, 0.18);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.73);
  padding: 10px 5px;
  box-sizing: border-box;
  position: relative;
}

.AutoCompleteSearch .ClearSearch {
  position: absolute;
  z-index: 1;
  cursor: default;
  width: 25px;
  height: 25px;
  right: 1%;
  top: 0;
  bottom: 0;
  margin: auto;
  font-size: 170%;
  background: #ffffff;
  border-radius: 50%;
  border: 1px solid #cfcece;
}

.AutoCompleteSearch ul {
  position: absolute;
  z-index: 1;
  list-style-type: none;
  text-align: left;
  margin: auto;
  padding: 0;
  border: 1px solid gray;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 4px 1px rgba(0, 0, 0, 0.18);
  width: 100%;
  background-color: white;
  left: 50%;
  -webkit-transform: translate(-50.0005%, 0);
          transform: translate(-50.0005%, 0);
  max-height: 180px;
  overflow: auto;
}

.AutoCompleteSearch ul::before {
  content: "";
}

.AutoCompleteSearch li {
  padding: 10px 5px;
  cursor: pointer;
  position: relative;
  z-index: 1;
}

.AutoCompleteSearch li:hover {
  text-decoration: underline;
  color: white;
  background-color: rgba(38, 38, 114, 0.92);
}

.test {
  position: relative;
}

.Options {
  width: 90%;
  margin: auto;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-flow: wrap;
}
.Box {
  border: 1px solid black;
  background-color: rgb(196, 226, 255);
  width: 24%;
  box-sizing: border-box;
  padding: 0.5%;
}

.OptionsHeader {
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
}

.Box input {
  width: 45%;
  border: 1px solid gray;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 4px 1px rgba(0, 0, 0, 0.18);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.73);
  box-sizing: border-box;
  padding: 1%;
}

.buttonSort {
  color: white;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  padding: 2%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  width: 45%;
  font-size: 1vw;
  margin: 4px 2px;
  border-radius: 12px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.buttonExport {
  background-color: #ffc400;
  color: black;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  padding: 1%;
  text-align: center;
  border: 2px solid gray;
  display: inline-block;
  width: 15vw;
  font-size: 1.2vw;
  margin: auto;
  border-radius: 12px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* Add mouse hover effect for buttons. For some reason still works on some mobile devices.*/
@media (pointer: fine) {
  .buttonSort:hover {
    background-color: rgb(0, 250, 21);
    border: 2px solid white;
  }

  .buttonExport:hover {
    background-color: yellow;
    border: 2px solid white;
    color: gray;
  }

  .buttonManual:hover {
    background-color: #2b9dfa;
    border: 2px solid white;
  }

  .buttonReset:hover {
    background-color: #fc4949;
    border: 2px solid white;
  }

  .buttonSort:active {
    background-color: rgb(171, 241, 177);
    box-shadow: none;
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
  }

  .buttonExport:active {
    background-color: yellow;
    box-shadow: none;
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
  }

  .buttonManual:active {
    background-color: #2b9dfa;
    box-shadow: none;
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
  }

  .buttonReset:active {
    background-color: #fc4949;
    box-shadow: none;
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
  }

  .modal .close:hover {
    color: white;
    background-color: #4bd5ff;
  }

  .AutoCompleteSearch .ClearSearch:hover {
    color: white;
    background-color: #4bd5ff;
  }

  .AutoCompleteSearch .ClearSearch:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
  }
}

.highlighted {
  background-color: rgb(0, 250, 21);
  border: 2px solid white;
}

.notHighlighted {
  background-color: #4caf50;
  border: 2px solid gray;
}

hr {
  width: 90%;
}

.Table {
  width: 90%;
  margin: auto;
}

.Table table {
  margin: auto;
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 90%;
  table-layout: fixed;
  font-size: 1vw;
}

.Table table th,
td {
  padding: 1%;
}

th,
td {
  border-bottom: 1px solid #ddd;
}

.hundo {
  background-color: #4bd5ff;
}

.ninetyEight {
  background-color: #9dff65;
}

.ninetySix {
  background-color: #f7fa63;
}

.ninetyThree {
  background-color: rgb(255, 194, 80);
}

.ninetyOne {
  background-color: rgb(236, 125, 125);
}

.nundo {
  background-color: rgb(201, 201, 201);
}

h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 2vw;
}

footer {
  bottom: 0;
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1vw;
}

.tableImageHide {
  display: none;
}

.tableImage {
  width: 80%;
  margin: auto;
  display: flex;
}

.tableImage .Block {
  margin: auto;
  width: 20%;
  height: 20%;
}

#BlockLeft {
  line-height: 1.5em;
  font-size: 1.5vw;
  font-family: proxima-soft, sans-serif;
  font-style: normal;
  font-weight: 800;
}

.BlockLeftHeader {
  font-size: 2vw;
}

#BlockRight {
  width: 20%;
  font-size: 80%;
  font-family: proxima-soft, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.TypeIcon {
  width: 20%;
  height: 20%;
}

.Header .Instructions {
  font-size: 1em;
}

.modal {
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  height: 600px;
  overflow: auto;
}
.modal .header {
  width: 100%;
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.modal .content {
  width: 90%;
  padding: 10px 5px;
  margin: auto;
  font-size: 1vw;
}

.modal .content li {
  text-align: left;
}
.modal .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}
.modal .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}

.buttonManual {
  background-color: #005296;
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  padding: 0.5%;
  text-align: center;
  border: 2px solid gray;
  display: inline-block;
  width: 8vw;
  font-size: 0.8vw;
  margin: auto;
  border-radius: 12px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.buttonReset {
  background-color: #c00202;
  color: white;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  padding: 0.5%;
  text-align: center;
  border: 2px solid gray;
  display: inline-block;
  width: 8vw;
  font-size: 0.8vw;
  margin: auto;
  border-radius: 12px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media only screen and (max-width: 600px) {
  .Table table {
    font-size: 2.5vw;
    width: 100%;
  }

  .Table table th,
  td {
    padding: 0.1%;
  }

  .Table table th {
    font-size: 1.5vw;
  }

  h1 {
    font-size: 4vw;
  }

  #BlockLeft {
    font-size: 2vw;
  }

  .BlockLeftHeader {
    font-size: 2.3vw;
  }

  #BlockRight {
    font-size: 1.5vw;
  }

  .TypeIcon {
    width: 25%;
    height: 25%;
  }
  .Box {
    width: 45%;
    margin: 5px;
  }

  .buttonSort {
    width: 90%;
    font-size: 3vw;
  }

  .buttonExport {
    width: 25%;
    font-size: 1.75vw;
  }

  .buttonManual {
    width: 40%;
    font-size: 0.8em;
  }

  .buttonReset {
    width: 15%;
    font-size: 1.75vw;
  }

  .Header .Instructions {
    font-size: 0.8em;
  }

  .modal .content {
    font-size: 1em;
    overflow-y: scroll;
  }

  .Box input {
    width: 90%;
  }
}

@media only screen and (max-height: 600px) and (orientation: portrait) {
  .modal {
    height: 120vw;
  }
}

@media only screen and (max-height: 600px) and (orientation: landscape) {
  .modal {
    height: 40vw;
  }
}

